import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { LocalStorageService } from '../../../node_modules/ngx-webstorage';
import { ObjectUnsubscribedError } from '../../../node_modules/rxjs';

@Component({
  selector: 'app-products',
  templateUrl: './products.component.html',
  styleUrls: ['./products.component.css'],
  inputs: [`filtertext`]
})
export class ProductsComponent implements OnInit {

  public prodData: object;
  public cartData = [];
  public temp:string;
  public nextId : number;
  public filtertext:string;


      constructor(private http:HttpClient, private localStore:LocalStorageService){
        this.nextId = 0; 
        this.http.get('assets/data/products.json').subscribe((data)=> this.prodData=data);
      }

  
  ngOnInit() {
  }

  
  setCartStore(obj){
    //  console.log((JSON.parse(obj.target.id)));
  
    this.cartData[this.nextId] =  obj;
  
    this.temp = JSON.stringify(this.cartData);

    // console.log(JSON.parse(this.temp));

    this.localStore.store("cartData",this.temp);
    this.nextId = this.nextId + 1;
    this.getCartStore();
  }

  getCartStore(){

    console.log(this.localStore.retrieve('cartData'));
  }

}
