import { NgModule, Input } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {ProductsComponent} from './products/products.component';
import {BillingComponent} from './billing/billing.component';
import {CartComponent} from './cart/cart.component';


const routes: Routes = [
  {path:'showProducts', component: ProductsComponent},
  {path:'billFinal', component: BillingComponent},
  {path:'showCart', component: CartComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {}
export const routingComponent = [ProductsComponent, BillingComponent, CartComponent]
