import { Component } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { LocalStorageService } from '../../node_modules/ngx-webstorage';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
      public prodData: object;
    public filtertext : string;
      constructor(private http:HttpClient, private localStore: LocalStorageService){
          this.http.get('assets/data/products.json').subscribe((data)=> this.prodData=data);
      }

      saveFilter(){
        this.localStore.store("filtertext",this.filtertext);
      }


}



